import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import MainHeader from "../../components/Users/MainHeader";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Overlay,
  Popup,
  CloseBtn,
  styles,
  CardImageContainer,
  CardImage,
  TitlePay,
  Form,
  FormGroup,
  Label,
  Input,
  Row,
  ButtonGroup,
  Button,
} from "../../components/Styles/MainStyle.js";
import {
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import { useParams } from "react-router-dom";
import moment from "moment";
import { momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { format, toZonedTime } from "date-fns-tz";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
export default function Subscription() {
  const localizer = momentLocalizer(moment);
  const [plans, setplans] = useState([]);
  const [selectedPeriod, setSelectedPeriod] = useState("monthly"); // default monthly
  const [showPopup, setShowPopup] = useState(false);
  const [planId, setplanId] = useState("");
  const apiUrl = "https://blueprintcatalyst.com/api/user/";
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  useEffect(() => {
    getallSubscriptionPlan();
  }, []);
  const getallSubscriptionPlan = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(
        apiUrl + "getallSubscriptionPlan",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      console.log(respo);
      setplans(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const monthlyPlans = plans.filter((plan) => plan.period === "monthly");
  const yearlyPlans = plans.filter((plan) => plan.period === "yearly");

  //Payment
  const handlechooseplan = (id) => {
    setplanId(id);
    setShowPopup(true);
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    // handle form submission
    console.log(e.target);
    var vl = e.target;
    let formData = {
      name: vl.name.value,
      email: vl.email.value,
      cardnumber: vl.cardnumber.value,
      expiry: vl.expiry.value,
      cvv: vl.cvv.value,
      user_id: userLogin.id,
      plan_id: planId,
    };
    try {
      const res = await axios.post(apiUrl + "usersubscription", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    setShowPopup(false);
  };
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />

          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row justify-content-center">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>
                <div className="col-md-9">
                  <div
                    className="d-flex justify-content-end mb-4 gap-2"
                    role="group"
                    aria-label="Billing period toggle"
                  >
                    <button
                      type="button"
                      className={`btn btn-outline-dark ${
                        selectedPeriod === "monthly" ? "active" : ""
                      }`}
                      onClick={() => setSelectedPeriod("monthly")}
                    >
                      Monthly
                    </button>
                    <button
                      type="button"
                      className={`btn btn-outline-dark ${
                        selectedPeriod === "yearly" ? "active" : ""
                      }`}
                      onClick={() => setSelectedPeriod("yearly")}
                    >
                      Yearly
                    </button>
                  </div>

                  <div className="row g-4 justify-content-center">
                    {(selectedPeriod === "monthly"
                      ? monthlyPlans
                      : yearlyPlans
                    ).map((plan) => (
                      <div className="col-md-4" key={plan.id}>
                        <div className="card h-100 shadow">
                          <div className="card-header text-center subscriptionplan text-white">
                            <h4 className="mb-0">{plan.name}</h4>
                          </div>
                          <div className="card-body text-center">
                            <h1 className="card-title fw-bold">
                              ${plan.price}
                              <small className="text-muted fs-6">
                                /{selectedPeriod === "monthly" ? "mo" : "year"}
                              </small>
                            </h1>
                            <ul className="list-unstyled mt-3 mb-4">
                              <li>
                                âœ” {plan.module}{" "}
                                {parseInt(plan.module) === 1
                                  ? "module"
                                  : "modules"}
                              </li>
                            </ul>

                            <button
                              className="btn subscriptionplan text-white w-100"
                              type="button"
                              onClick={() => handlechooseplan(plan.id)}
                            >
                              Choose {plan.name}
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
      {showPopup && (
        <Overlay>
          <Popup>
            <CloseBtn onClick={() => setShowPopup(false)}>Ã—</CloseBtn>
            <TitlePay>Payment</TitlePay>
            <CardImageContainer>
              <CardImage src="/assets/user/images/cardimage.jpg" alt="cards" />
            </CardImageContainer>
            <Form onSubmit={handleSubmit}>
              <FormGroup>
                <Label>Name</Label>
                <Input type="text" name="name" required placeholder="name" />
              </FormGroup>
              <FormGroup>
                <Label>Email</Label>
                <Input type="email" name="email" required placeholder="email" />
              </FormGroup>
              <FormGroup>
                <Label>Card Number</Label>
                <Input
                  type="text"
                  name="cardnumber"
                  required
                  placeholder="card number"
                  inputMode="numeric"
                  maxLength={19} // 16 digits + 3 spaces
                  onInput={(e) => {
                    let value = e.target.value.replace(/\D/g, ""); // Remove non-digits
                    value = value.substring(0, 16); // Max 16 digits
                    value = value.replace(/(.{4})/g, "$1 ").trim(); // Add space every 4 digits
                    e.target.value = value;
                  }}
                />
              </FormGroup>

              <Row>
                <FormGroup>
                  <Label>Expiry Date</Label>
                  <Input
                    type="text"
                    name="expiry"
                    required
                    placeholder="MM/YYYY"
                    inputMode="numeric"
                    maxLength={7}
                    pattern="(0[1-9]|1[0-2])\/\d{4}" // MM/YYYY format
                    onInput={(e) => {
                      let value = e.target.value.replace(/\D/g, ""); // remove non-digits
                      if (value.length > 6) value = value.slice(0, 6);
                      if (value.length >= 3) {
                        value = value.slice(0, 2) + "/" + value.slice(2);
                      }
                      e.target.value = value;
                    }}
                  />
                </FormGroup>

                <FormGroup>
                  <Label>CVV</Label>
                  <Input type="text" name="cvv" required placeholder="123" />
                </FormGroup>
              </Row>
              <ButtonGroup>
                <Button
                  type="button"
                  className="cancel"
                  onClick={() => setShowPopup(false)}
                >
                  Cancel
                </Button>
                <Button type="submit" className="submit">
                  Pay Now
                </Button>
              </ButtonGroup>
            </Form>
          </Popup>
        </Overlay>
      )}
    </>
  );
}
